import React from "react";
import Car from "./Car";
import "../css/Main.css";
import cars from "../cars";


function Main() {
    return (
        <div className="car-list">
            {cars.map((car) => (
                <Car
                    id={car.id}
                    model={car.model}
                    img={car.img}
                    price={car.price}
                    details={car.details}
                />
            ))}
        </div>
    );
}
export default Main;